import { useEffect, useRef, useState  } from "react";
import './admin.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import Header from "../components/header";
import AdminCategory from "./admin-category";
import AdminProduct from "./admin-product";

const Admin = ()=>{
    const menuItem = [
        <Link to="/" className="link" key="1">Qun l n hng</Link>,
        <Link to="/admin/quan-ly-danh-muc" className="link" key="2" >Qun l danh mc</Link>,
        <Link to="/admin/quan-ly-san-pham" className="link" key="3">Qun l sn phm</Link>,
        <Link to="/" className="link" key="4">Qun l in n</Link>,
        <Link to="/in-tai-lieu" className="link" key="5">Qun l bo co</Link>,
    ];
    // const menuItem = [
    //     <div className="link" onClick={()=>{ history.push('/admin/quan-ly-danh-muc');}}>Qun l danh mc</div>,
    //     <div className="link" onClick={()=>{history.push('/admin/quan-ly-san-pham');}}>Qun l sn phm</div>,
    // ]

    return(
        <Router>
            <div className="admin">
                <div className="container">
                    <Header menuItems={menuItem}></Header>
                    <div className="view">
                        <Switch>
                            <Route exact path="/admin/quan-ly-danh-muc">   
                                <AdminCategory/>
                            </Route>
                            <Route exact path="/admin/quan-ly-san-pham">   
                                <AdminProduct/>
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </Router>
    );
}

export default Admin;