const confirmationPopupContent = {
    confirmationImage: "https://unblast.com/wp-content/uploads/2021/08/Online-Order-Illustration.jpg",
    confirmationContent: ()=>{return(
        <>
            <p>Dino Shop cm n bn  n vi Dino Shop. Dino Shop xin c xc nhn bn va thc hin vic ng k vi Dino h tr mua v in mt s sn phm.</p> 
            <p>Bn vui lng xc nhn li gip t l bn c nhu cu mua hng v chc chn nhn hng v khng c hu n hng sau khi xc nhn. V khi bn t hng v khng nhn s b x l theo quy nh ca trng.</p>
        </>
    )}
}

const Cart = ()=>{
    const [showPopup, setShowPopup] = useState(0);
    const [popupName, setPopupName] = useState("feedback");
    const [orderData, setOrderData] = useState("");

    const showOrderSuccessPopup = ()=>{
        createOrder(orderData);
        setPopupName('order-success');
    }

    const showConfirmationPopup = (_data)=>{
        setOrderData(_data);
        setPopupName("confirmation");
        setShowPopup(1);
    } 

    const hidePopup = ()=>{
        setShowPopup(0);
    }

    return(
        <CartContext.Consumer>
            {
                context => (
                    <div style={{marginTop: "30px"}}>
                        <StudentInfo 
                            name="Trn Minh Hng" 
                            id="FSC10252" 
                            class="11A3" 
                            years="2021-2022" 
                            phone="0907689756" 
                            email="tranminhhung10252@fpt.edu.vn"
                            avatar="https://getbootstrap.com.vn/blog/wp-content/uploads/2020/11/kieu-toc-hoc-sinh-two-block.jpg"
                        ></StudentInfo>
                        <CartList data = {((context.orderReviewMode === 1)&&(context.orderReviewData !== null)) ? [context.orderReviewData] : context.cartData} mode={context.orderReviewMode === 1? "order-review" : "cart"} handlers={{showConfirmation: showConfirmationPopup}}></CartList>
                        <Popup 
                            name={popupName} 
                            data={confirmationPopupContent}
                            orderData = {orderData} 
                            show={showPopup} 
                            handlers={{
                                showOrderSuccess: showOrderSuccessPopup, 
                                hide: hidePopup 
                        }}></Popup>
                    </div>
                )
            }
        </CartContext.Consumer>
       
    );
};

export default Cart;