import './student-info.css';

const StudentInfo = (props)=>{
    return(
        <div className = "student-info">
            <div className="container">
                <h3>Thng tin hc sinh</h3>
                <div className="info-group">
                    <div className="info" style={{flex: 1}}><div className="avatar"><img src={props.avatar} alt="hnh hc sinh"/></div></div>
                    <div className="info"><div>H v tn:</div><div>{props.name}</div></div>
                    <div className="info"><div>MSHS:</div><div>{props.id}</div></div>
                    <div className="info"><div>Lp:</div><div>{props.class}</div></div>
                    <div className="info"><div>Nin kha:</div><div>{props.years}</div></div>
                    <div className="info"><div>S in thoi:</div><div>{props.phone}</div></div>
                    <div className="info" style={{flex:3}}><div>Email:</div><div>{props.email}</div></div>
                </div>
            </div>
        </div>
    );
}

export default StudentInfo;