
import { useEffect, useRef, useState  } from "react";
import Button from './button';
import Dropdown from "./dropdown";
import './popup.css'
import RadioButton from './radio-button';


const FeedbackContent = (props)=>{

    const [content,setContent] = useState("");

    const updateFeedback = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            props.handlers.showFeedbackSuccess(props.orderID,content);
        }
    }

    return(
        <div className="feedback">
            <h3>Bo co n hng</h3>
            <p>{props.orderID}-{props.orderType}</p>
            <p><span>Ngy t hng:</span> {props.orderDate}</p>
            <textarea value={content} onChange={(event)=>{setContent(event.target.value)}}></textarea>
            <div className="buttons">
                <Button label="Hy" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                <Button label="Gi bo co" onClick={typeof(props.handlers)!=="undefined" ? updateFeedback : null}/>
            </div>
            
        </div>
    );
}

const FeedbackSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <p style={{textAlign:"center"}}>Bn  gi feedback thnh cng</p>
            <div className="buttons">
                <Button label="ng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
            
        </div>
    );
}

const CancelContent = (props)=>{
    return(
        <div className="feedback">
            <p style={{textAlign:"center"}}>Bn c chc chn mun hy n hng ny ({props.id}) hay khng?</p>
            <div className="buttons">
                <Button label="Khng" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                <Button label="C" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}


const ConfirmationContent = (props)=>{
    return(
        <div className="feedback">
            <div className="row">
                <div className="image">
                    <img src={typeof(props.data) !== "undefined" ? props.data.confirmationImage: "" } alt="hnh trang tr"></img>
                </div>
                <div className="content">
                    {typeof(props.data) !== "undefined" ? props.data.confirmationContent() : "" }
                    <div className="buttons">
                        <Button label="Hy" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                        <Button label="Xc nhn t hng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.showOrderSuccess : null}/>
                    </div>
                </div>
            </div>
        </div>
    );
}

const OrderSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <h3 style={{textAlign:"center"}}>t hng thnh cng</h3>
            <p style={{textAlign:"center"}}>n hng ca bn s c ban kim duyt nh trng x l. Bn c th hy n hng trc khi n hng c duyt. Nu c vn  cn gii quyt, vui lng s dng tnh nng bo co  gi bo co n nh trng.</p>
            <div className="buttons">
                <Button label="ng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}

const PrintOrderSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <h3 style={{textAlign:"center"}}>ng k thnh cng</h3>
            <p style={{textAlign:"center"}}>Bn  ng k in thnh cng! Bn vui lng i nhn ti liu sau th 4, th 6 hng tun</p>
            <div className="buttons">
                <Button label="Tip tc mua hng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}

const CreateCategory = (props)=>{
    const [ncc, setNcc] = useState("");
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [active,setActive] = useState(true);

    const activeCat = ()=>{
        setActive(true);
    }

    const unActiveCat = ()=>{
        setActive(false);
    }

    const createCategory = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            props.handlers.createCategory({name: name, provider: ncc, description: description, status: active}, props.id);
            setTimeout(()=>{
                setNcc("");
                setName("");
                setDescription("");
                setActive(true);
            },500);  
        } 
    }

    const renderAddButton = ()=>{
        if (typeof(props.handlers) !== "undefined"){
            if((typeof(props.handlers.updateFlag) !== "undefined") && (props.handlers.updateFlag === 1)){
                return(<Button label={"Cp nht"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
            }else{
                return(<Button label={"Thm"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
            }
        }else{
            return(<Button label={"Thm"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
        }
    }

    useEffect(()=>{
        if(props.data){
            let data = JSON.parse(props.data);
            setNcc(data.provider);
            setName(data.name);
            setDescription(data.description);
            setActive(data.show);
        } 
    },[props.data]);

    return(
        <div className="create-category">
            <h3>Thm danh mc</h3>
            <label>Tn danh mc</label>
            <input type="text" value={name} onChange={(event)=>{setName(event.target.value);}} placeholder = "Nhp tn danh mc ti y" />
            <label>M t</label>
            <textarea value={description} onChange={(event)=>{setDescription(event.target.value);}} placeholder = "Nhp m t ti y" />
            <label>Nh cung cp</label>
            <input type="text" value={ncc} onChange={(event)=>{setNcc(event.target.value);}} placeholder = "Nhp tn nh cung cp ti y" />
            <div className="selection-group">
                <label>Trng thi</label>
                <div className="option">
                    <RadioButton label="Hin th" selected={active === true ? 1 : 0} onClick={()=>{activeCat();}}/>
                    <RadioButton label="n" selected={active === true ? 0 : 1} onClick={()=>{unActiveCat();}}/>
                </div>
            </div>
            <div className="button-group">
                <Button label="Quay li" isWeak={true} style={{margin: "25px 15px"}} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hideCreateCategoryPopup : null}/>
                {renderAddButton()}
            </div>
        </div>
    );
}

const CreateProduct = (props)=>{
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [price, setPrice] = useState("");
    const [productCode, setProductCode] = useState("");
    const [category,setCategory] = useState("");
    const [active,setActive] = useState(true);
    const [thumbnail,setThumbnail] = useState("");
    const [file, setFile] = useState(null);
    const [loading, setLoading] = useState(0);

    const activeProd = ()=>{
        setActive(true);
    }

    const unActiveProd = ()=>{
        setActive(false);
    }

    const createProduct = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            setLoading(1);
            props.handlers.createProduct({
                name: name, 
                description: description, 
                status: active, 
                price: price, 
                code: productCode,
                category:category  ,
                thumbnail: thumbnail,
                file: file
            },()=>{setLoading(0);});
            setTimeout(()=>{
                setCategory("");
                setName("");
                setDescription("");
                setPrice("");
                setProductCode("");
                setFile(null);
                setActive(true);
            },500);  
        } 
    }

    const renderAddButton = ()=>{
        if (typeof(props.handlers) !== "undefined"){
            if((typeof(props.handlers.updateFlag) !== "undefined") && (props.handlers.updateFlag === 1)){
                return(<Button label={"Cp nht"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
            }else{
                return(<Button label={"Thm"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
            }
        }else{
            return(<Button label={"Thm"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
        }
    }

    useEffect(()=>{
        if(props.data){
            let data = JSON.parse(props.data);
            setName(data.name);
            setDescription(data.description);
            setPrice(data.price);
            setProductCode(data.code);
            setCategory(data.category);
            setActive(data.show);
            setThumbnail(data.thumbnail)
        } 
    },[props.data]);

    return(
        <div className="create-category create-product" style={{opacity: loading === 1 ? 0.3 : 1 }}>
            <h3>Thm sn phm</h3>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Thuc danh mc</label>
                   <Dropdown  placeholder="Chn danh mc" options={typeof(props.dropdownData) !== "undefined" ? typeof(props.dropdownData.category) !== "undefined" ? props.dropdownData.category :[] : []}  onClick = {(value)=>{setCategory(value);}}></Dropdown>
                </div>
                <div className="col">
                    <label>M sn phm</label>
                    <input type="text" value={productCode} onChange={(event)=>{setProductCode(event.target.value);}} placeholder = "Nhp m sn phm" />
                </div>
            </div>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Tn sn phm</label>
                    <input type="text" value={name} onChange={(event)=>{setName(event.target.value);}} placeholder = "Nhp tn sn phm" />
                </div>
                <div className="col">
                    <label>Gi tin</label>
                    <input type="text" value={price} onChange={(event)=>{setPrice(event.target.value);}} placeholder = "Nhp gi tin" />
                </div>
            </div>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Hnh nh</label>
                    <input type="file" accept="image/*" onChange={(event)=>{setFile(event.target.files[0])}}/>
                    <div className="selection-group">
                        <label>Trng thi</label>
                        <div className="option">
                            <RadioButton label="Hin th" selected={active === true ? 1 : 0} onClick={()=>{activeProd();}}/>
                            <RadioButton label="n" selected={active === true ? 0 : 1} onClick={()=>{unActiveProd();}}/>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <label>M t</label>
                    <textarea value={description} onChange={(event)=>{setDescription(event.target.value);}} placeholder = "Nhp m t ti y" />
                </div>
            </div>
            
           
            <div className="button-group">
                <Button label="Quay li" isWeak={true} style={{margin: "25px 15px"}} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hideCreateCategoryPopup : null}/>
                {renderAddButton()}
            </div>
        </div>
    );
}
const Popup = (props)=>{

    const [show,setShow] = useState(props.show);
    const [popupName, setPopupName] = useState(props.name);

    useEffect(()=>{
        setPopupName(props.name);
    },[props.name])

    const showContent = ()=>{
        switch (popupName){
            case "feedback": 
                return <FeedbackContent 
                    orderID = {props.data.orderID} 
                    orderType = {props.data.orderType} 
                    orderDate={props.data.orderDate}
                    handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}
                />;
            case "feedback-success":
                return <FeedbackSuccessContent  handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>   
            case "cancel-order":
                return <CancelContent id={typeof(props.data) !== "undefined" ? props.data.id : null} handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "confirmation":
                return <ConfirmationContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null}/>
            case "order-success":
                return <OrderSuccessContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "print-order-success":
                return <PrintOrderSuccessContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "create-category":
                 return <CreateCategory handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null}/>
            case "create-product":
                return <CreateProduct handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null} dropdownData={props.dropdownData}/>        
            
            default:
                return null;
        }
    }

    useEffect(()=>{
        setShow(props.show);
    },[props.show]);

    return (
        <div className={show === 1 ? "popup show" : "popup"}>
            <div className="wrapper">
                <div className="overlay"></div>
                <div className="container">
                    {
                      showContent()  
                    }
                </div>
            </div>
        </div>
    );
}

export default Popup;